export function validateEmail(email) {
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
}

function validatePhone(phone) {
	return phone.test(/^(?:(?:\(?(?:00|\+)([1-4]\d\d|[1-9]\d?)\)?)?[\-\.\ \\\/]?)?((?:\(?\d{1,}\)?[\-\.\ \\\/]?){0,})(?:[\-\.\ \\\/]?(?:#|ext\.?|extension|x)[\-\.\ \\\/]?(\d+))?$/i)
}

export function ValidateFormSteps(current_form_values, current_form_steps) {
	// console.log(current_form_values)
	// console.log(current_form_steps.length)
	for (let i=0; i<current_form_steps.length; i++) {
		let temp_input_item = current_form_values[current_form_steps[i].form_name]
		if (current_form_steps[i].validate.length > 0 && temp_input_item !== undefined) {
			let validation_array = current_form_steps[i].validate.split(',');
			let valid = false;
			for (let v=0; v<validation_array.length; v++) {
				if (v == 0 && validation_array[v] !== "") {
					switch (validation_array[0]) {
						case "text":
							valid = /^\w+( \w+)*$/.test(temp_input_item)
							break;
						case "number":
							valid = /^\d+$/.test(temp_input_item);
							break;
						case "email":
							valid = validateEmail(temp_input_item)
							break;
						case "phone":
							valid = validatePhone(temp_input_item)
							break;
					}
				}
				else if (/^[0-9-]/.test(validation_array[v])) {
					let lengths = validation_array[v].split('-')
					if (lengths[0] && lengths[1]) {
						valid = temp_input_item.length > parseInt(lengths[0]) && temp_input_item.length < parseInt(lengths[1])
					}
					else if (lengths[0] && !lengths[1]) {
						if (temp_input_item.length > parseInt(lengths[0])) {
							valid = true
						}
						else {
							console.error("value too short")
							valid = false
						}
					}
					else if (!lengths[0] && lengths[1]) {
						if (temp_input_item.length < parseInt(lengths[1])) {
							valid = true
						}
						else {
							console.error("value too long")
							valid = false
						}
					}
				}
			}
			return valid
		}
		else if (current_form_steps[i].validate.length === 0) {
			return true
		}	
	}
	return false
}


