<template>

<div id="sss-cart">
	<div class="row" :style="{'background-color': plans[current_plan].color, 'color': current_plan == 2 ? 'white' : 'black' }">
		<div class="col-xs-6 col-md-4">
			<h2>${{ getTotal }} / yr</h2>
		</div>
		<div class="col-md-4">
			<div class="text-center">Plan
			<div class="form-group">
				<select class="form-control" v-model="current_plan">
					<option v-for="(p, index) in plans" :value="index">{{p.name}}</option>
				</select>
			</div>
			</div>
			
		</div>
		<div class="col-xs-6 col-md-4">
			<h2 class="text-right">View Cart</h2>
		</div>
	</div>
	<h1 class="text-center">Cart</h1>
	<table class="table">
		<thead>
			<th>Brand</th>
			<th>Model</th>
			<th>Price</th>
			<th>Quantity</th>
			<th>Options</th>
		</thead>
		<tbody>
			<tr v-for="(item, index) in cart">
				<td>{{item.brand}}</td>
				<td>{{item.model}}</td>
				<td>${{ getPrice(item.multiplier)}}</td>
				<td>
					<input type="number" value="1" name="quantity">
				</td>
				<td>
					Edit |
					<button class="btn btn-sm" type="button" @click="removeItem(item.id, index)">Remove</button>
				</td>
			</tr>
		</tbody>
	</table>
</div>

</template>

<script>

import { mapGetters, mapActions } from 'vuex'

export default {
	data () {
		return {
			current_plan: 0
		}
	},
	methods: {
		...mapActions([
			'removeCartItem'
		]),
		getPrice(mult) {
			let subtotal = this.plans[this.current_plan].cost * mult
			return subtotal.toFixed(2)
		},
		removeItem(id, index) {
			this.removeCartItem({
				id: id,
				index: index
			})
		}
	},
	computed: {
		...mapGetters({
			cart: 'getCart',
			plans: 'getPlans',
		}),
		getTotal() {
			let total = 0;
			for (let i=0; i<this.cart.length; i++) {
				total += this.cart[i].multiplier * this.plans[this.current_plan].cost
			}
			return total.toFixed(2)
		}
	}
}

</script>

<style lang="scss">

</style>


